import React from "react";

import styled from "styled-components";
import { motion } from "framer-motion";

import { data } from "../assets/Text.js";

import Premium from "../assets/premium1.png";
import Searches from "../assets/searches1.png";
import Updated from "../assets/updated1.png";

export const WhyNow = () => {
  return (
    <Styles className="setPadding">
      <h3>{data.whyNowTitle}</h3>
      <div className="reasons">
        <div className="why">
          <img src={Premium} alt="100% discount" />
          <p>{data.reason1}</p>
        </div>
        <div className="why">
          <img src={Updated} alt="Constructing build process" />

          <p>{data.reason2}</p>
        </div>
        <div className="why">
          <img src={Searches} alt="Man with an aim. Top of the searches." />
          <p>{data.reason3}</p>
        </div>
      </div>
    </Styles>
  );
};

const Styles = styled(motion.div)`
  padding: 20px;
  min-width: 100%;
  .reasons {
    margin-top: 60px;
    display: grid;
    min-width: 100%;
    gap: 30px;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
  .why {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    img {
      width: 100px;
    }
  }
  p {
    margin-top: 20px;
    text-align: center;
  }
`;
