import React from "react";

import { data } from "../assets/Text.js";

import styled from "styled-components";
import { motion } from "framer-motion";

import { textAnime } from "../Animations";

import { InputComponent } from "./InputComponent";

import PlayArrowIcon from "@material-ui/icons/PlayArrow";

import { useMediaQuery } from "react-responsive";

import ImageHead from "../assets/sports-bg.jpeg";

function ScrollDownWeb() {
  window.scroll({
    top: 850,
    behavior: "smooth",
  });
}
function ScrollDownApp() {
  window.scroll({
    top: 1550,
    behavior: "smooth",
  });
}
export const Heading = ({ modal, setModal, value, setValue }) => {
  const isMobile = useMediaQuery({
    query: "(max-device-width: 700px)",
  });
  return (
    <Styles
      variants={textAnime}
      initial="initial"
      animate="animate"
      className="setPadding"
    >
      <div>
        <h1>{data.mainTitle}</h1>
        <div className="description">
          <p>{data.mainDescription}</p>
        </div>
        <div className="input">
          <InputComponent
            modal={modal}
            setModal={setModal}
            value={value}
            setValue={setValue}
          />
        </div>
      </div>
      <div
        className="btn drop-shadow"
        onClick={isMobile ? ScrollDownApp : ScrollDownWeb}
      >
        <PlayArrowIcon />
        <span>{data.watchBtn}</span>
      </div>
    </Styles>
  );
};

const Styles = styled(motion.div)`
  color: white;
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  margin-top: 40px;
  .image {
    img {
      display: block;
      margin-left: auto;
      margin-right: auto;
      width: calc(100vw - 10px);
      z-index: -100;
      height: 100vh;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  div {
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }

  .description {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    p {
      margin: 20px 0;
      max-width: 500px;
      text-align: center;
      font-size:24px;
    }
  }

  .input {
    margin-top: 40px;
  }
  .btn {
    background: white;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 10px 20px 10px 10px;
    border: 1px solid #5fdf4e;
    border-radius: 2px;
    color: #5fdf4e;
    transition: all 0.5s ease-out;
    font-weight: 500;

    :hover {
      svg {
        transform: rotate(90deg);
      }
    }
    svg {
      font-size: 28px;
      transition: all 0.3s ease-out;
    }
    span {
      margin-left: 10px;
    }
  }
`;
