import React, { useState, useEffect } from "react";

import styled from "styled-components";
import { motion } from "framer-motion";

export const Cookies = ({ setCookies }) => {
  const HideCookies = () => {
    setCookies(false);
  };
  return (
    <Styles>
      <p>🍪 This website is using cookies to improve your experience.</p>
      <div className="flex-btns">
        <div className="accept" onClick={HideCookies}>
          Accept
        </div>
        <div className="cancel">Read more</div>
      </div>
    </Styles>
  );
};

const Styles = styled(motion.div)`
  position: fixed;
  bottom: 20px;
  left: 20px;
  background: white;
  padding: 20px;
  text-align: center;
  border: 1px solid grey;
  border-radius: 25px;
  z-index: 200;

  p {
    width: 260px;
    font-weight: 500;
  }
  .flex-btns {
    margin-top: 15px;
    display: flex;
    justify-content: center;
  }
  .cancel,
  .accept {
    padding: 10px;
    border-radius: 25px;
    cursor: pointer;
    margin-left: 10px;
  }
  .accept {
    border: 1px solid grey;
    padding-left: 15px;
    padding-right: 15px;
    transition: all 0.3s ease-in-out;
    :hover {
      background: #5fdf4e;
      color: white;
      border: 1px solid #5fdf4e;
    }
  }

  p,
  button,
  div {
    font-size: 14px;
  }
`;
