import React, { useState, useEffect } from "react";

import styled from "styled-components";
import { motion, AnimatePresence } from "framer-motion";

import Logo from "../assets/Logo.png";
import { data } from "../assets/Text.js";

import {
  useScrollPosition,
  useScrollXPosition,
  useScrollYPosition,
} from "react-use-scroll-position";

function ScrowToTop() {
  window.scroll({
    top: 0,
    behavior: "smooth",
  });
}
//Go to enter your email section
function ScrowToCTA() {
  window.scroll({
    top: 2250,
    behavior: "smooth",
  });
}

export const Header = () => {
  const [navBtn, setNavBtn] = useState(false);

  const { x, y } = useScrollPosition();
  const scrollX = useScrollXPosition();
  const scrollY = useScrollYPosition();

  useEffect(() => {
    if (scrollY > 550) {
      setNavBtn(true);
    } else {
      setNavBtn(false);
    }
  }, [scrollY]);

  return (
    <Styles className="setPadding">
      <div className="wholeNav">
        <div className="logo" onClick={ScrowToTop}>
          <img src={Logo} alt="Star athlete logo" />
          <h5>{data.logoLabel}</h5>
        </div>
        <AnimatePresence>
          {navBtn && (
            <motion.button
              initial={{ opacity: 0, y: -10 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -50 }}
              className="drop-shadow"
              id="header-button"
              onClick={ScrowToCTA}
            >
              {data.buttonNav}
            </motion.button>
          )}
        </AnimatePresence>
      </div>
    </Styles>
  );
};

const Styles = styled(motion.div)`
  display: flex;
  align-items: center;
  justify-content: center;
  position: sticky;
  top: 0;
  left: 0;
  padding-top: 10px;
  padding-bottom: 0;
  z-index: 3;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(11px);
  -webkit-backdrop-filter: blur(11px);
  width: 100%;

  .wholeNav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 60px;
    max-width: 1400px;
  }
  #header-button {
    width: 140px;
  }
  .logo {
    display: flex;
    align-items: center;
    cursor: pointer;
    img {
      width: 40px;
      margin-right: 10px;
    }
  }
`;
