import React, { useState, useEffect, Suspense } from "react";

import styled from "styled-components";
import { motion } from "framer-motion";

import { Header } from "./sections/Header";
import { Heading } from "./sections/Heading";

import { Video } from "./sections/Video";
import { Usp } from "./sections/Usp";
import { BigCta } from "./sections/BigCta";
import { WhyNow } from "./sections/WhyNow";
import { Footer } from "./sections/Footer";
import { Cookies } from "./sections/Cookies";

import ImageHead from "./assets/sports-bg 1.jpg";

import { Modal } from "./sections/Modal";

// const Video = lazy(() => import("./sections/Video"));
// const Usp = lazy(() => import("./sections/Usp"));

export const HomePage = () => {
  //understand when to open the component for successful registration
  const [modal, setModal] = useState(false);
  //get the value from the input
  const [value, setValue] = useState("");
  //Hide cookies
  const [openCookies, setCookies] = useState(true);

  useEffect(() => {
    if (modal) document.body.style.overflow = "hidden";
    else document.body.style.overflow = "visible";
  }, [modal]);
  return (
    <Styles>
      <div className="bg">
        <div class="layer"></div>
        <img src={ImageHead} alt="Field and stadium image" />
      </div>

      {openCookies && <Cookies setCookies={setCookies} />}
      {modal && <Modal value={value} setModal={setModal} />}
      <Header />
      <Heading
        modal={modal}
        setModal={setModal}
        value={value}
        setValue={setValue}
      />
      <Suspense fallback={<div>Loading...</div>} maxDuration={2000}>
        <Video />
      </Suspense>

      <div className="content ">
        <Suspense fallback={<div>Loading...</div>} maxDuration={2000}>
          <Usp />
        </Suspense>
      </div>
      <BigCta
        modal={modal}
        setModal={setModal}
        value={value}
        setValue={setValue}
      />
      <div className="content ">
        <WhyNow />
      </div>
      <Footer />
    </Styles>
  );
};

const Styles = styled(motion.div)`
  .content {
    margin: 0 auto;
    max-width: 1200px;
  }
  .bg {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1000;
    filter: blur(1.5px);
    width: 100%;
    .layer {
      background-color: rgb(34, 40, 40, 0.3);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: calc(100% - 10px);
    }

    img {
      height: calc(100vh + 40px);
      width: 100%;
      object-fit: none;
    }
  }
`;
