import React, { useEffect, useState } from "react";
import { useMediaQuery } from "react-responsive";

import styled from "styled-components";
import { motion } from "framer-motion";

import { data } from "../assets/Text.js";
import Connecting from "../assets/connecting1.png";
import Searching from "../assets/searching1.jpg";
import Showcase from "../assets/showcase1.jpg";

export const Usp = () => {
  const isDesktop = useMediaQuery({
    query: "(min-width: 1024px)",
  });

  return (
    <Styles className="setPadding">
      <h1 className="why">{data.uspTitle}</h1>
      <div className="usps ">
        <div className="usp">
          <div className="text">
            <h3>{data.uspTitle1}</h3>
            {!isDesktop && (
              <img className="drop-shadow-img" src={Connecting} alt="Man on computer connecting with scouts" />
            )}
            <p>{data.uspeDescription1}</p>
          </div>
          {isDesktop && (
            <img className="drop-shadow-img" src={Connecting} alt="Man on computer connecting with scouts" />
          )}
        </div>
        <div className="usp">
          {isDesktop && (
            <img className="drop-shadow-img" src={Showcase} alt="Man on a rocket showing his skills" />
          )}
          <div className="text">
            <h3>{data.uspTitle2}</h3>
            {!isDesktop && (
              <img className="drop-shadow-img" src={Showcase} alt="Man on a rocket showing his skills" />
            )}
            <p>{data.uspeDescription2}</p>
          </div>
        </div>
        <div className="usp">
          <div className="text">
            <h3>{data.uspTitle3}</h3>
            {!isDesktop && (
              <img className="drop-shadow-img" src={Searching} alt="Man searching for sport athletes" />
            )}
            <p>{data.uspeDescription3}</p>
          </div>
          {isDesktop && (
            <img className="drop-shadow-img" src={Searching} alt="Man searching for sport athletes" />
          )}
        </div>
      </div>
    </Styles>
  );
};

const Styles = styled(motion.div)`
  padding: 40px 20px;

  h1 {
    text-align: center;
  }
  img {
    max-width:100%;
    max-height: 380px;
    margin: 20px 0;
  }
  h3 {
    margin-top: 40px;
  }
  //Screens bigger than 700px -tablets and PC
  @media screen and (min-width: 700px) {
    margin-top: 60px;

    .usp {
      margin-bottom: 80px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      img {
        max-height: 550px;
        max-width: 750px;
        padding: 60px;
      }
      p {
        margin-top: 20px;
        max-width: 500px;
      }
      h3 {
        max-width: 580px;
      }
      :last-child {
        margin-bottom: 0px;
      }
    }
  }
`;
